.t-badge{-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;position:relative}.t-badge__inner{background-color:#e34d59;color:hsla(0,0%,100%,.9);display:inline-block;font-size:10px;font-weight:400;height:16px;line-height:16px;min-width:16px;padding:0 4px;text-align:center;z-index:100}.t-badge--has-children{position:absolute;right:0;top:0;-webkit-transform:translate(50%,-50%);transform:translate(50%,-50%)}.t-badge--dot{border-radius:50%;height:10px;min-width:10px;padding:0}.t-badge--small{-webkit-transform:translate(50%,-50%) scale(.75);transform:translate(50%,-50%) scale(.75)}.t-badge--circle{border-radius:8px}.t-badge--round{border-radius:4px}.t-badge__ribbon--outer{display:inline-block;position:absolute;right:0;top:0}.t-badge--ribbon{padding:0;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.t-badge--ribbon:before{border-left:16px solid transparent;left:-16px}.t-badge--ribbon:after,.t-badge--ribbon:before{border-bottom:16px solid #e34d59;bottom:0;content:"";height:0;position:absolute;width:0}.t-badge--ribbon:after{border-right:16px solid transparent;right:-16px}